import { Box, Button, HStack, Menu, MenuButton, MenuItem, MenuItemProps, MenuList, Text } from '@chakra-ui/react'
import { ChevronDownIcon } from '@chakra-ui/icons'
import React from 'react'
import { MenuButtonProps } from '@chakra-ui/menu/dist/menu-button'
import { ALL_DISTRICT, ALL_REGION } from '../hooks/usePlaceFilter'
import { TextProps } from '@chakra-ui/layout/dist/text'

const MENU_BTN_STYLE: MenuButtonProps = {
  w: { base: '100%', lg: '268px' },
  h: { base: '80px', lg: '48px' },
  textAlign: 'left',
  fontSize: '18px',
  fontWeight: 400,
  color: '#2D3748',
  backgroundColor: '#F0E8CD',
  borderRadius: { base: 0, lg: '6px' },
  border: { base: 0, lg: '1px solid #E2E8F0' },
  borderTop: { base: '1px solid #F0E8CD', lg: 0 },
  borderBottom: { base: '2px solid #F0E8CD', lg: 0 },
  pt: { base: '10px', lg: 0 },
  _hover: {
    backgroundColor: '#EDE9DC',
  },
  _focus: {
    backgroundColor: '#F0E8CD',
  },
  _active: {
    backgroundColor: '#EDE9DC',
  },
}

const MENU_ITEM_STYLE: MenuItemProps = {
  w: { base: '100vw', lg: '268px' },
  pl: '20px',
  _hover: {
    backgroundColor: '#F0E8CD',
  },
  _focus: {
    backgroundColor: '#F0E8CD',
  },
}

const HINT_STYLE: TextProps = {
  fontSize: '12px',
  color: '#858585',
  ml: '5px',
  mb: '5px',
  position: { base: 'absolute', lg: 'relative' },
  top: { base: '13px', lg: 'unset' },
  left: { base: '12px', lg: 'unset' },
  zIndex: { base: 1, lg: 'unset' },
}

interface PlaceFilterProps {
  selectedRegion: string
  selectedDistrict: string
  regionList: string[]
  regionDistrictMap: Record<string, string[]>
  handleSelectRegion: (region: string) => void
  handleSelectDistrict: (district: string) => void
  isDisableSelectDistrict: boolean
}

export function PlaceFilter(props: PlaceFilterProps) {
  const {
    selectedRegion,
    selectedDistrict,
    regionList,
    regionDistrictMap,
    isDisableSelectDistrict,
    handleSelectRegion,
    handleSelectDistrict,
  } = props

  return (
    <HStack gap={{ base: 0, lg: '24px' }}>
      <Box flex={1} position="relative">
        <Text {...HINT_STYLE}>選擇區域</Text>
        <Menu placement="bottom">
          <MenuButton as={Button} rightIcon={<ChevronDownIcon />} {...MENU_BTN_STYLE}>
            {selectedRegion}
          </MenuButton>
          <MenuList w={MENU_ITEM_STYLE.w}>
            <MenuItem onClick={() => handleSelectRegion(ALL_REGION)} {...MENU_ITEM_STYLE}>
              {ALL_REGION}
            </MenuItem>
            {regionList.map((region, i) => (
              <MenuItem key={i} onClick={() => handleSelectRegion(region)} {...MENU_ITEM_STYLE}>
                {region}
              </MenuItem>
            ))}
          </MenuList>
        </Menu>
      </Box>
      <Box flex={1} position="relative">
        <Text {...HINT_STYLE}>選擇地區</Text>
        <Menu>
          <MenuButton
            as={Button}
            rightIcon={<ChevronDownIcon />}
            {...MENU_BTN_STYLE}
            isDisabled={isDisableSelectDistrict}
          >
            {selectedDistrict}
          </MenuButton>
          <MenuList w={MENU_ITEM_STYLE.w}>
            <MenuItem onClick={() => handleSelectDistrict(ALL_DISTRICT)} {...MENU_ITEM_STYLE}>
              {ALL_DISTRICT}
            </MenuItem>
            {regionDistrictMap[selectedRegion]?.map((district, i) => (
              <MenuItem key={i} onClick={() => handleSelectDistrict(district)} {...MENU_ITEM_STYLE}>
                {district}
              </MenuItem>
            ))}
          </MenuList>
        </Menu>
      </Box>
    </HStack>
  )
}
