import { AspectRatio, Box, Flex, Heading, HStack, Image, Link, useBreakpointValue, VStack } from '@chakra-ui/react'
import Map from './Map'
import React, { useCallback, useEffect, useRef, useState } from 'react'
import useFetchPlaceData from '../hooks/useFetchPlaceData'
import PlaceCard from './PlaceCard'
import { Place } from '../types'
import { PlaceFilter } from './PlaceFilter'
import { usePlaceFilter } from '../hooks/usePlaceFilter'
import { getStaticImgUrl } from '../utils'

const FIXED_FILTER_SCROLL_Y = 250

export function SalePlace() {
  const { placeList, regionList, regionDistrictMap, tagMap } = useFetchPlaceData()

  const cardListRef = useRef<HTMLDivElement>(null)
  const filterBoxRef = useRef<HTMLDivElement>(null)

  const [showIsFixedFilter, setShowIsFixedFilter] = useState(false)
  const canShowFixedFilter = useBreakpointValue({
    base: true,
    lg: false,
  })

  const [selectedPlace, setSelectedPlace] = useState<Place | null>(null)

  const onChangePlaceFilter = useCallback(() => {
    cardListRef.current?.scrollTo({ top: 0 })
    window.scrollTo({ top: 0 })
  }, [])

  const [
    selectedRegion,
    selectedDistrict,
    handleSelectRegion,
    handleSelectDistrict,
    filteredPlaceList,
    isDisableSelectDistrict,
  ] = usePlaceFilter(placeList, setSelectedPlace, onChangePlaceFilter)

  useEffect(() => {
    const handleScroll = () => {
      if (window.scrollY > FIXED_FILTER_SCROLL_Y) {
        setShowIsFixedFilter(true)
      } else {
        setShowIsFixedFilter(false)
      }
    }
    window.addEventListener('scroll', handleScroll)
    return () => {
      window.removeEventListener('scroll', handleScroll)
    }
  }, [])

  const onClickPlaceCard = useCallback((place: Place) => {
    setSelectedPlace(place)
  }, [])

  const cardListHeight = useBreakpointValue({
    base: '100%',
    lg: `${window.innerHeight - (filterBoxRef.current?.clientHeight ?? 0) - 165}px`,
  })

  return (
    <Box bgColor="#F9F9F9">
      {canShowFixedFilter && showIsFixedFilter && (
        <Box position="fixed" left={0} top={0} w="100%" zIndex={1000} bg="white">
          <PlaceFilter
            selectedRegion={selectedRegion}
            selectedDistrict={selectedDistrict}
            regionList={regionList}
            regionDistrictMap={regionDistrictMap}
            isDisableSelectDistrict={isDisableSelectDistrict}
            handleSelectRegion={handleSelectRegion}
            handleSelectDistrict={handleSelectDistrict}
          />
        </Box>
      )}

      <Flex
        bgColor="#F5F5F5"
        flexDirection={{ base: 'column', lg: 'row' }}
        p={{ base: '20px 17px', lg: 0 }}
        mb={{ base: 0, lg: '34px' }}
      >
        <Box
          flex={1}
          bgImage={{ base: 'none', lg: getStaticImgUrl('cooperation-bg.svg') }}
          bgSize="cover"
          bgPosition="right"
          textAlign={{ base: 'center', lg: 'right' }}
        >
          <Heading
            as="p"
            fontSize="20px"
            fontWeight={400}
            color="black"
            mt={{ base: 0, lg: '24px' }}
            mb={{ base: '11px', lg: '24px' }}
            mr={{ base: 0, lg: '102px' }}
          >
            合作通路夥伴
          </Heading>
        </Box>
        <Box flex={{ base: 1, lg: 2, xl: 1.8, '2xl': 1.5 }} m={{ base: '0 auto', lg: 0 }}>
          <HStack spacing={{ base: '10px', lg: '60px' }} alignItems="center" h="100%" pl={{ base: 0, lg: '65px' }}>
            <Link
              href="https://www.amazon.com/stores/page/D7611F97-8EBA-45C3-B640-36BF542539CC?ingress=3"
              target="_blank"
            >
              <Image src={getStaticImgUrl('amazon-logo.svg')} h={{ base: '33px', lg: '50px' }} />
            </Link>
            <Link href="https://www.norbelbaby.com.tw/nec/about?page=stronghold" target="_blank">
              <Image src={getStaticImgUrl('tingting-logo.svg')} h={{ base: '23px', lg: '29px' }} />
            </Link>
            <Link href="https://logon.com.hk/pages/store-locator" target="_blank">
              <Image src={getStaticImgUrl('log-on-logo.svg')} h={{ base: '26px', lg: '29px' }} />
            </Link>
          </HStack>
        </Box>
      </Flex>
      <Flex flexDirection="row" gap="30px" w={{ base: '100%', lg: '900px', xl: '1180px', '2xl': '1436px' }} m="0 auto">
        <Box w={{ base: '100%', lg: '580px' }}>
          <Box ref={filterBoxRef}>
            <PlaceFilter
              selectedRegion={selectedRegion}
              selectedDistrict={selectedDistrict}
              regionList={regionList}
              regionDistrictMap={regionDistrictMap}
              isDisableSelectDistrict={isDisableSelectDistrict}
              handleSelectRegion={handleSelectRegion}
              handleSelectDistrict={handleSelectDistrict}
            />
          </Box>

          <VStack
            ref={cardListRef}
            spacing="20px"
            mt="20px"
            h={cardListHeight}
            overflowY={{ base: 'unset', lg: 'scroll' }}
            ml={{ base: '15px', lg: 'unset' }}
            mr={{ base: '15px', lg: 'unset' }}
          >
            {filteredPlaceList.map((place, i) => (
              <Box key={i} w="100%">
                <PlaceCard
                  place={place}
                  tagMap={tagMap}
                  isSelected={place === selectedPlace}
                  onClickPlaceCard={onClickPlaceCard}
                />
              </Box>
            ))}
            <Box minH="100px"></Box>
          </VStack>
        </Box>

        <Box flex={1} hideBelow="lg" pt="50px">
          <AspectRatio ratio={1} w="100%">
            <Map query={selectedPlace?.address?.gmap ?? 'taipei'} />
          </AspectRatio>
        </Box>
      </Flex>
    </Box>
  )
}
