import { Box } from '@chakra-ui/react'
import { GMAP_KEY } from '../define'

interface MapProps {
  query: string
}

export default function Map(props: MapProps) {
  const { query } = props
  return (
    <iframe
      loading="lazy"
      allowFullScreen
      referrerPolicy="no-referrer-when-downgrade"
      src={buildMapUrl(query)}
    ></iframe>
  )
}

function buildMapUrl(q: string): string {
  return `https://www.google.com/maps/embed/v1/place?key=${GMAP_KEY}&q=${q}`
}
