import {
  Card,
  CardBody,
  CardFooter,
  Heading,
  HStack,
  Image,
  VStack,
  Text,
  Link,
  Flex,
  Tag,
  Box,
  AspectRatio,
  Center,
  useBreakpointValue,
  Collapse,
} from '@chakra-ui/react'
import { Place, TagMap } from '../types'
import { getHostedImgUrl, getStaticImgUrl } from '../utils'
import { useCallback, useEffect, useState } from 'react'
import Map from './Map'
import { HTMLChakraProps } from '@chakra-ui/system'

interface PlaceCardProps {
  place: Place
  tagMap: TagMap
  isSelected: boolean
  onClickPlaceCard: (place: Place) => void
}

const TEXT_STYLE = {
  color: '#3D3D3D',
  fontSize: '16px',
  fontStyle: 'normal',
  fontWeight: 400,
  lineHeight: 'normal',
}

const DEFAULT_TAG_COLOR = '#E7E7E7'

export default function PlaceCard(props: PlaceCardProps) {
  const { place, tagMap, isSelected, onClickPlaceCard } = props
  const [isShowMap, setIsShowMap] = useState(false)
  const [lastPlace, setLastPlace] = useState<Place | null>(null)

  useEffect(() => {
    if (lastPlace !== place) {
      setIsShowMap(false)
      setLastPlace(place)
    }
  }, [lastPlace, place])

  const canShowCardMap = useBreakpointValue({
    base: true,
    lg: false,
  })

  const canShowSelectedBorder = useBreakpointValue({
    base: false,
    lg: true,
  })

  const handleUserClick = useCallback(() => {
    if (canShowCardMap) {
      if (place.address?.gmap) {
        setIsShowMap(!isShowMap)
      }
    } else {
      onClickPlaceCard(place)
    }
  }, [canShowCardMap, isShowMap, onClickPlaceCard, place])

  const extraCardStyle: HTMLChakraProps<'div'> = {}
  if (isSelected && canShowSelectedBorder) {
    extraCardStyle['border'] = '2px solid #F0E8CD'
  }

  return (
    <Card onClick={handleUserClick} cursor="pointer" {...extraCardStyle}>
      <CardBody>
        <VStack spacing="20px" alignItems="flex-start">
          {place.imgName && <Image src={getHostedImgUrl(place.imgName)} alt={place.name} />}
          <Flex width="100%" alignItems="center">
            <Box flex={1}>
              <Heading as="h3" fontSize="24px" color="#595959" fontStyle="normal" fontWeight={600} lineHeight="normal">
                {place.name}
              </Heading>
            </Box>
          </Flex>

          <VStack spacing="15px" alignItems="flex-start">
            <Box>
              {place.typeList.map((type, i) => (
                <Link
                  href={tagMap[type]?.link ?? ''}
                  target="_blank"
                  key={i}
                  onClick={(e) => e.stopPropagation()}
                  mr="5px"
                  mb="7px"
                  display="inline-block"
                >
                  <Tag fontSize="13px" padding="10px" backgroundColor={tagMap[type]?.color ?? DEFAULT_TAG_COLOR}>
                    {type}
                  </Tag>
                </Link>
              ))}
            </Box>

            {place.address?.text && (
              <HStack spacing="13px" justifyContent="flex-start">
                <Image src={getStaticImgUrl('bxs-map-pin.svg')} alt="address" w="24px" h="24px"></Image>
                <Text {...TEXT_STYLE}>{place.address.text}</Text>
              </HStack>
            )}

            {place.time && (
              <HStack spacing="13px">
                <Image src={getStaticImgUrl('bx-time.svg')} alt="time" w="24px" h="24px"></Image>
                <Text {...TEXT_STYLE}>{place.time}</Text>
              </HStack>
            )}

            {place.phone && (
              <HStack spacing="13px">
                <Image src={getStaticImgUrl('bx-phone.svg')} alt="phone" w="24px" h="24px"></Image>
                <Text {...TEXT_STYLE}>{place.phone}</Text>
              </HStack>
            )}

            {place.lineUrl && (
              <HStack spacing="13px">
                <Image src={getStaticImgUrl('line-icon.svg')} alt="line" w="24px" h="24px"></Image>
                <Link href={place.lineUrl} textDecorationLine="underline" {...TEXT_STYLE}>
                  Line@點我
                </Link>
              </HStack>
            )}

            {place.link && (
              <HStack spacing="13px">
                <Image src={getStaticImgUrl('open-in-view.svg')} alt="line" w="24px" h="24px"></Image>
                <Link href={place.link} target="_blank" textDecorationLine="underline" {...TEXT_STYLE} color="#3E4AB7">
                  查看地點
                </Link>
              </HStack>
            )}
          </VStack>
        </VStack>
        <Collapse in={!!(canShowCardMap && isShowMap && place.address?.gmap)} animateOpacity>
          <Box mt="20px">
            <AspectRatio ratio={1} w="100%">
              {isShowMap ? <Map query={place.address?.gmap || ''} /> : <Box></Box>}
            </AspectRatio>
          </Box>
        </Collapse>
      </CardBody>
      {place.address?.gmap && (
        <CardFooter flexDir="column" gap="15px" borderTop="1px solid #F0E9DF" p="10px" hideFrom="lg">
          <Center>
            <HStack spacing="5px" cursor="pointer" onClick={handleUserClick}>
              <Text {...TEXT_STYLE}>{isShowMap ? '關閉地圖' : '查看地圖'}</Text>
              <Image
                src={getStaticImgUrl(isShowMap ? 'bx-chevron-up.svg' : 'bx-chevron-down.svg')}
                alt="close"
                w="24px"
                h="24px"
              ></Image>
            </HStack>
          </Center>
        </CardFooter>
      )}
    </Card>
  )
}
