import { useEffect, useState } from 'react'
import { Place, PlaceRaw, RegionDistrictMap, TagMap, TagRaw } from '../types'
import Papa, { ParseRemoteConfig } from 'papaparse'
import { PLACE_DATA_URL } from '../define'

export default function useFetchPlaceData() {
  const [placeList, setPlaceList] = useState<Array<Place>>([])
  const [tagMap, setTagMap] = useState<TagMap>({})
  const [regionList, setRegionList] = useState<Array<string>>([])
  const [regionDistrictMap, setRegionDistrictMap] = useState<RegionDistrictMap>({})

  useEffect(() => {
    const doFetchPlace = async () => {
      const { placeList, regionList, regionDistrictMap } = await fetchPlaceData()
      setPlaceList(placeList)
      setRegionList(regionList)
      setRegionDistrictMap(regionDistrictMap)
    }

    const doFetchTag = async () => {
      const tagMap = await fetchTagData()
      setTagMap(tagMap)
    }

    Promise.all([doFetchPlace(), doFetchTag()])
  }, [])

  return {
    placeList,
    regionList,
    regionDistrictMap,
    tagMap,
  }
}

async function fetchCsv<T>(url: string) {
  return new Promise<Array<T>>((resolve, reject) => {
    Papa.parse(url, {
      download: true,
      header: true,
      dynamicTyping: true,
      complete: (results) => {
        resolve(results.data)
      },
      error: (error) => {
        reject(error)
      },
    } as ParseRemoteConfig)
  })
}

async function fetchPlaceData() {
  if (!PLACE_DATA_URL) {
    console.log('PLACE_DATA_URL is empty, please check .env file')
    return {
      placeList: [],
      regionList: [],
      regionDistrictMap: {},
    }
  }
  const placeRawList = await fetchCsv<PlaceRaw>(`${PLACE_DATA_URL}/place.csv`)
  const placeList: Array<Place> = placeRawList.map((placeRaw) => {
    const { region, district, name, address, addressGmap, phone, time, type, link, imgName, lineUrl } = placeRaw
    return {
      region,
      district,
      name,
      address: {
        text: address,
        gmap: addressGmap,
      },
      phone,
      time,
      typeList: type ? type.split('|') : [],
      link,
      imgName,
      lineUrl,
    }
  })

  const regionList: Array<string> = []
  const regionDistrictMap: RegionDistrictMap = placeList.reduce((acc, place) => {
    const { region, district } = place
    if (!acc[region]) {
      acc[region] = []
      regionList.push(region)
    }
    if (district && !acc[region].includes(district)) {
      acc[region].push(district)
    }
    return acc
  }, {} as RegionDistrictMap)

  return {
    placeList,
    regionList,
    regionDistrictMap,
  }
}

async function fetchTagData() {
  if (!PLACE_DATA_URL) {
    console.log('PLACE_DATA_URL is empty, please check .env file')
    return {}
  }
  const tagRawList = await fetchCsv<TagRaw>(`${PLACE_DATA_URL}/tag.csv`)
  return tagRawList.reduce((acc, tagRaw) => {
    const { name, color, link } = tagRaw
    acc[name] = { color, link }
    return acc
  }, {} as TagMap)
}
